@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
.nav {
  height: 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__logo {
  display: flex;
  width: 200px;
}

.nav__toggle {
  font-size: 1.2rem;
  color: white;
  cursor: pointer;
}

.hamburger {
  width: 12px;
}

@media screen and (max-width: 767px) {
  .nav__menu {
    position: fixed;
    background-color: hsl(206, 4%, 6%);
    top: -100%;
    left: 0;
    width: 100%;
    padding: 4rem 0 3rem;
    transition: 0.4s;
  }
}

.nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-row-gap: 1rem;
  row-gap: 1rem;
}

.nav__link {
  color: white;
  font-size: 1.25rem;
  text-transform: uppercase;
  font-weight: 600;
  background: linear-gradient(hsl(0, 0%, 49%), hsl(206, 4%, 8%));
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  transition: 0.4s;
}

.nav__link:hover {
  background: white;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

.nav__close {
  position: absolute;
  font-size: 1.5rem;
  top: 1rem;
  right: 1rem;
  color: white;
  cursor: pointer;
}

/* show menu */
.show-menu {
  top: 0;
}

/* Change background header */
.scroll-header {
  background-color: hsl(206, 4%, 6%);
}

/* Active link */
.active-link {
  background: white;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

@media screen and (min-width: 767px) {
  .nav {
    height: calc(var(--header-height) + 1.5rem);
  }
  .nav__logo {
    width: 200px;
  }
  .nav__list {
    flex-direction: row;
    grid-column-gap: 3.5rem;
    -webkit-column-gap: 3.5rem;
            column-gap: 3.5rem;
  }
  .nav__link {
    font-size: var(--normal-font-size);
    text-transform: none;
    text-transform: initial;
    padding: 1rem;
  }
  .nav__toggle,
  .nav__close {
    display: none;
  }
}

