.specs__container {
  position: relative;
}

.specs__content {
  grid-row-gap: 1.5rem;
  row-gap: 1.5rem;
}

.specs__data {
  display: grid;
  grid-row-gap: 0.25rem;
  row-gap: 0.25rem;
}

.specs__icon {
  font-size: 1.2rem;
  color: #fff;
  color: var(--white-color);
}

.specs__title {
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  font-weight: 500;
  font-weight: var(--font-medium);
}

